<template>
  <div class="hs-container">
    <hs-header :title="$t('base.Change')" isBackNav></hs-header>

    <hs-scroll class="tableView" :data="[details]">
      <ul>

        <train-segment :detail="originalDetails" :title="$t('base.OriginalSchedule')">
          <hs-cell-1 :showLogo="false" @click.native="_getChangeDetails">
            <div class="cell-segment-one">
              <p>{{originalDetails.SeatTypeName}}</p>
              <p>{{$t('base.ChangeInstructions')}}</p>
            </div>
          </hs-cell-1>
        </train-segment>

        <hs-section v-if="trainOrder && trainOrder.GoSelectedRoute" :title="$t('base.Rescheduling')">
          <hs-cell-1 :showLogo="false">
            <div class="cell-segment">
              <div class="cell-segment-left">
                <p>{{$t(beTime(trainOrder.GoSelectedRoute.DepartureDateTime), 'date', ['MM月dd日', 'MM dd'])}} {{getWeeks(beTime(trainOrder.GoSelectedRoute.DepartureDateTime))}}</p>
                <p>{{beTime(trainOrder.GoSelectedRoute.DepartureDateTime,'hh:mm')}}</p>
                <p>{{trainOrder.GoSelectedRoute.DepartStationName}}</p>
              </div>
              <div class="cell-segment-middle">
                <p>{{trainOrder.GoSelectedRoute.TrainCode}}</p>
                <div class="cell-segment-time">
                  <div class="layer"></div>
                </div>
                <p>{{trainOrder.GoSelectedRoute.RunTime}}</p>
              </div>
              <div class="cell-segment-right">
                <p>{{$t(beTime(trainOrder.GoSelectedRoute.ArrivalDateTime), 'date', ['MM月dd日', 'MM dd'])}} {{getWeeks(beTime(trainOrder.GoSelectedRoute.ArrivalDateTime))}}</p>
                <p>{{beTime(trainOrder.GoSelectedRoute.ArrivalDateTime,'hh:mm')}}<span class="ArrivalDays" v-if="trainOrder.GoSelectedRoute.ArrivalDays > 0">+{{trainOrder.GoSelectedRoute.ArrivalDays}}</span></p>
                <p>{{trainOrder.GoSelectedRoute.ArrivalStationName}}</p>
              </div>
            </div>
          </hs-cell-1>
          <hs-cell-1 :showLogo="false" @click.native="_getChangeDetails">
            <div class="cell-segment-one" :type="language">
              <p>{{trainOrder.GoSelectedRoute.TypeName}}</p>
              <p>{{$t('base.ChangeInstructions')}}</p>
            </div>
          </hs-cell-1>
        </hs-section>

        <hs-section title="#" v-if="details && details.Route">
          <div class="tips" slot="title">*具体价格以12306审核为准</div>
          <hs-cell-1 @click.native="_select(index)"
            v-for="(item, index) in details.Route.Tickets" :key="index"
            :showLogo="false">
            <div class="custom-cell">
              <div class="left">
                <p class="name">{{item.PsgName}}</p>
                <p>{{item.Credential.CredentialName}}&nbsp;{{item.Credential.CredentialNo}}</p>
              </div>
              <div class="right" v-if="details.Route.Tickets[index]">
                <div class="items">
                  <div class="item">
                    <p>差价</p>
                    <p class="price">¥{{details.DiffPrice}}</p>
                  </div>
                  <div class="item">
                    <p>改签费</p>
                    <p class="price">¥{{details.ChangePrice}}</p>
                  </div>
                </div>
                <div class="checkbox" style="background-image: url('static/image/base/check-box/check-box.png')" v-show="details.Route.Tickets[index].select"></div>
                <div class="checkbox" style="background-image: url('static/image/base/check-box/check-box-default.png')" v-show="!details.Route.Tickets[index].select"></div>
              </div>
            </div>
          </hs-cell-1>
        </hs-section>

      </ul>
    </hs-scroll>

    <div class="footer">
      <hs-gradient-button type="auto" width="60%" height="40px" :title="$t('base.Change')" @click="_gotoChange"></hs-gradient-button>
    </div>

    <!-- 改签规则 -->
    <hs-dialog v-model="showRule">
      <hs-scroll class="ruleDialog" :data="[ruleDetails]">
        <ul>
          <div v-html="ruleDetails"></div>
        </ul>
      </hs-scroll>
    </hs-dialog>

  </div>
</template>

<script type='text/ecmascript-6'>
import axios from 'axios'
import eventBus from 'common/js/eventBus.js'
import HsCell1 from 'base/hs-tableview/hs-cell-1/hs-cell-1'
import HsDialog from 'base/hs-dialog/hs-dialog'
import HsGradientButton from 'base/hs-button/hs-gradient-button'
import HsHeader from 'components/c-header/c-header'
import HsScroll from 'base/hs-scroll/hs-scroll'
import HsSection from 'base/hs-tableview/hs-section/hs-section'
import TrainSegment from 'pages/train/share/train-segment'
import {mapGetters, mapMutations} from 'vuex'
import {kpiTrainGetChangeInitInfo, kpiTrainSubmitChange} from 'api'
export default {
  created() {
    if (this.noValue(this.trainOrder.DepartStationCode)) {
      this.$router.replace('/home')
      return
    }
    this._getDetails()
  },
  computed: {
    ...mapGetters([
      'trainOrder'
    ]),
    /** 改签原订单 */
    originalDetails() {
      return JSON.parse(this.$route.query.originalDetails)
    }
  },
  data() {
    return {
      details: {},
      showRule: false,
      ruleDetails: ''
    }
  },
  methods: {
    ...mapMutations([
      'SET_TRAIN_ORDER'
    ]),
    _getDetails() {
      let params = {
        OrderID: this.trainOrder.OriginalOrderID,
        TrainCode: this.originalDetails.TrainCode,
        DepartStationCode: this.originalDetails.DepartStationCode,
        NewArrivalStationCode: this.trainOrder.ArrivalStationCode,
        SelectedTrainInfo: {
          TrainCode: this.trainOrder.GoSelectedRoute.TrainCode,
          SeatTypeCode: this.trainOrder.GoSelectedRoute.TypeCode,
          DepartDate: this.trainOrder.GoSelectedRoute.DepartureDateTime,
          ChooseSeats: this.trainOrder.GoSelectedRoute.ChooseSeats
        }
      }
      kpiTrainGetChangeInitInfo(params).then((res) => {
        console.log('火车票获取改签初始信息: ', res)
        this.details = res.ResultData
      })
    },
    /** 获取改签规则 */
    _getChangeDetails() {
      axios.get('/static/html/trainChange.html').then((res) => {
        this.ruleDetails = res.data
        this.showRule = true
      })
    },

    /** 选择 */
    _select(index) {
      this.details.Route.Tickets[index].select = !this.details.Route.Tickets[index].select
      this.$forceUpdate()
    },

    /** 去改签 */
    _gotoChange() {
      let dataList = this.details.Route.Tickets.filter(v => v.select).map(v => v.TicketID)
      if (dataList.length === 0) {
        this.$toast(this.$t('TM.PleaseSelectThePassengersToBeChanged'))
        return
      }
      let params = {
        TicketIds: dataList,
        DepartStationCode: this.originalDetails.DepartStationCode,
        ArrivalStationCode: this.trainOrder.ArrivalStationCode,
        SelectedTrainInfo: {
          TrainCode: this.trainOrder.GoSelectedRoute.TrainCode,
          SeatTypeCode: this.trainOrder.GoSelectedRoute.TypeCode,
          DepartDate: this.trainOrder.GoSelectedRoute.DepartureDateTime,
          ChooseSeats: this.trainOrder.GoSelectedRoute.ChooseSeats
        },
        OperateType: this.originalDetails.ArrivalStationCode === this.trainOrder.ArrivalStationCode ? 0 : 1
      }
      kpiTrainSubmitChange(params).then(res => {
        console.log('提交改签: ', res)
        if (res.ResultData.IsSuccess === true) {
          this.$toast(this.$t('TM.ChangeSuccessfully'))
          setTimeout(() => { eventBus.$emit(`refresh`) }, 0)
          this.$router.replace({path: '/train/orderDetail', query: {Id: this.trainOrder.OriginalOrderID, isOwner: true}})
        } else {
          this.$toast(this.$t('TM.ChangeFailed'))
        }
      })
    }
  },
  components: {
    HsCell1,
    HsDialog,
    HsGradientButton,
    HsHeader,
    HsScroll,
    HsSection,
    TrainSegment
  }
}
</script>
<style lang='stylus' scoped='' type='sylesheet/stylus'>
@import '~common/stylus/variable'
@import '~common/stylus/base'
.hs-container
  width 100%
  height 100%
  >.tableView
    width 100%
    height calc(100% - 45px - 50px)
    overflow hidden

    .cell-segment
      @extend .flex-row
      @extend .flex-between
      width 100%
      padding 15px 0
      .cell-segment-left
        @extend .flex-column
        @extend .flex-center
        width 33.33%
        line-height 1.4em
        >p
          &:nth-child(1)
            font-size 0.8 rem
          &:nth-child(2)
            font-size 1.2 rem
            font-weight bold
          &:nth-child(3)
            font-size 0.8 rem
      .cell-segment-middle
        @extend .flex-column
        @extend .flex-center
        width 33.33%
        line-height 1.4em
        >p
          &:nth-child(1)
            font-size 0.8 rem
            font-weight bold
          &:nth-child(3)
            font-size 0.6 rem
            color $color_gray_text
        >.cell-segment-time
          @extend .flex-center
          @extend .flex-row
          >.layer
            @extend .flex-center
            height 1px
            width 100px
            background-color $color_gray_text
      .cell-segment-right
        @extend .flex-column
        @extend .flex-center
        width 33.33%
        line-height 1.4em
        >p
          &:nth-child(1)
            font-size 0.8 rem
          &:nth-child(2)
            font-size 1.2 rem
            // color $color_black_text_1
            font-weight bold
            position relative
          &:nth-child(3)
            font-size 0.8 rem
        .ArrivalDays
          color $color_theme
          font-size 0.8rem
          position absolute
          top -4px
          right -16px

    .cell-segment-one
      @extend .flex-row
      @extend .flex-between
      width 100%
      >p
        font-size 0.8rem
        color $color_black_text_1

    // 提示
    .tips
      @extend .flex-row
      @extend .flex-start
      width 100%
      color $color_theme

    .custom-cell
      @extend .flex-row
      @extend .flex-between
      width 100%
      padding 15px 0px
      >.left
        width calc(100% - 150px)
        >.name
          font-size 1rem
          font-weight bold
          color $color_black_text_0
          margin-bottom 10px
        >p
          font-size 0.8rem
          color $color_black_text_1
      >.right
        @extend .flex-row
        @extend .flex-end
        width 150px
        >.items
          width 85px
          >.item
            @extend .flex-row
            @extend .flex-start
            margin-bottom 7px
            >p
              font-size 0.8rem
              color $color_black_text_1
            >.price
              color $color_theme
              margin-left 15px
            &:last-child
              margin-bottom 0
        >.checkbox
          width 20px
          height 20px
          background-size 100% 100%
          margin-left 15px

  >.footer
    @extend .flex-center
    width 100%
    height 50px
    border-top 1px solid $color_gray_layer

  .ruleDialog
    width 100%
    height 100%
</style>



